header {
  display: flex;
  padding: 1.25rem 1.5rem;
  background-color: hsl(0, 100%, 100%);
  align-items: center;
  justify-content: space-between;
  box-shadow: 0px 5px 10px hsla(var(--hue), var(--saturation), 20%, .15);
  position: relative;
}

header h2 {
  margin: 0;
  font-weight: 600;
  color: hsl(var(--hue), var(--saturation), 50%);
}

header p {
  margin: 0;
  font-weight: 500;
  font-size: 1.25rem;
  color: hsl(var(--hue), var(--saturation), 75%);
}

.success {
  position: absolute;
  height: 2.5rem;
  width: 6rem;
  bottom: -2.5rem;
  left: 50%;
  margin-left: -3rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 0 0 .5rem .5rem; 
  color: hsl(var(--hue), var(--saturation), var(--lightness));
  background-color: white;
  font-weight: 600;
  transform: translateY(-2.5rem);
  transition: transform .3s ease;
  z-index: -1;
}

.success.open {
  transform: translateY(0);
}

.btn {
  border: none;
  font-size: 1rem;
  font-weight: 500;
  border-radius: .3rem;
  padding: .75rem 1.25rem;
  background-color: hsl(var(--hue), var(--saturation), var(--lightness));
  color: hsl(var(--hue), var(--saturation), var(--lightness-ops));
  cursor: pointer;
  transition: all .2s ease;
}

.btn:hover {
  box-shadow: 0px 5px 15px hsla(var(--hue), var(--saturation), 50%, .25);
  transform: translateY(-.125rem)
}

.btn:focus {
  outline: none;
}

.btn-opposite {
  color: hsl(var(--hue), var(--saturation), var(--lightness));
  background-color: hsl(var(--hue), var(--saturation), var(--lightness-ops));
}